import { EventInput } from "@fullcalendar/core";
import config from "../../../config";

const BASE_URL = `${config.API_URL}/events`;

const userString: string | null = sessionStorage.getItem("ubold_user");
const user: any = userString && JSON.parse(userString);
const username: string | null = user?.username;

export const getAllEvents = async (): Promise<EventInput[]> => {
  try {
    const response = await fetch(BASE_URL);
    if (!response.ok) {
      throw new Error("Failed to fetch events");
    }

    const events: any[] = await response.json();
    // Convert string dates to Date objects
    return events
      .map((event) => ({
        ...event,
        start: new Date(event.start),
        end: event.end ? new Date(event.end) : null,
      }))
      .filter((x) => x.username === username);
  } catch (error: any) {
    console.error("Error fetching events:", error.message);
    throw error;
  }
};

export const getEventById = async (
  eventId: string
): Promise<EventInput | null> => {
  try {
    const response = await fetch(`${BASE_URL}/${eventId}`);
    if (!response.ok) {
      throw new Error("Event not found");
    }

    const eventData: any = await response.json();
    // Convert string dates to Date objects
    return {
      ...eventData,
      start: new Date(eventData.start),
      end: eventData.end ? new Date(eventData.end) : null,
    };
  } catch (error: any) {
    console.error(`Error fetching event with id ${eventId}:`, error.message);
    throw error;
  }
};

export const postEvent = async (eventData: EventInput): Promise<EventInput> => {
  try {
    return await fetch(BASE_URL, {
      method: "POST",
      headers: {
        "Content-Type": "application/json",
      },
      body: JSON.stringify({ ...eventData, username }),
    });
  } catch (error: any) {
    console.error("Error creating event:", error.message);
    throw error;
  }
};

export const putEvent = async (
  eventId: any,
  eventData: EventInput
): Promise<EventInput> => {
  try {
    return await fetch(`${BASE_URL}/${eventId}`, {
      method: "PUT",
      headers: {
        "Content-Type": "application/json",
      },
      body: JSON.stringify({ ...eventData, username }),
    });
  } catch (error: any) {
    console.error(`Error updating event with id ${eventId}:`, error.message);
    throw error;
  }
};

export const deleteEventById = async (eventId: any): Promise<void> => {
  try {
    const response = await fetch(`${BASE_URL}/${eventId}`, {
      method: "DELETE",
    });

    if (!response.ok) {
      throw new Error("Failed to delete event");
    }

    // No need to parse response for DELETE request
  } catch (error: any) {
    console.error(`Error deleting event with id ${eventId}:`, error.message);
    throw error;
  }
};
