import React from "react";
import FullCalendar from "@fullcalendar/react";
import dayGridPlugin from "@fullcalendar/daygrid";
import interactionPlugin from "@fullcalendar/interaction";
import timeGridPlugin from "@fullcalendar/timegrid";
import listPlugin from "@fullcalendar/list";
import BootstrapTheme from "@fullcalendar/bootstrap";
import { EventInput } from "@fullcalendar/core";

interface CalendarProps {
  onDateClick: (value: any) => void;
  onEventClick: (value: any) => void;
  onDrop: (value: any) => void;
  onEventDrop: (value: any) => void;
  onEventResize: (value: any) => void;
  events: EventInput[];
}

const Calendar = ({
  onDateClick,
  onEventClick,
  onDrop,
  onEventDrop,
  onEventResize,
  events,
}: CalendarProps) => {
  /*
   * handle calendar methods
   */
  const handleDateClick = (arg: any) => {
    onDateClick(arg);
  };
  const handleEventClick = (arg: any) => {
    onEventClick(arg);
  };
  const handleDrop = (arg: any) => {
    onDrop(arg);
  };
  const handleEventDrop = (arg: any) => {
    onEventDrop(arg);
  };

  // New handler for event resize
  const handleEventResize = (arg: any) => {
    // Extract start and end from the arg
    const { start, end, event } = arg;
    // Pass the resized event data to the parent component
    onEventResize({ start, end, event });
  };

  return (
    <>
      <div id="calendar">
        <FullCalendar
          initialView="timeGridWeek"
          plugins={[
            dayGridPlugin,
            interactionPlugin,
            timeGridPlugin,
            listPlugin,
            BootstrapTheme,
          ]}
          handleWindowResize={true}
          themeSystem="bootstrap"
          buttonText={{
            today: "Today",
            month: "Month",
            week: "Week",
            day: "Day",
            list: "List",
            prev: "Prev",
            next: "Next",
          }}
          headerToolbar={{
            left: "prev,next today",
            center: "title",
            right: "dayGridMonth,timeGridWeek,timeGridDay,listMonth",
          }}
          editable={true}
          selectable={true}
          droppable={true}
          events={events}
          dateClick={handleDateClick}
          eventClick={handleEventClick}
          drop={handleDrop}
          eventDrop={handleEventDrop}
          eventResize={handleEventResize}
          scrollTime="09:00:00"
        />
      </div>
    </>
  );
};

export default Calendar;
