import React, { useEffect, useState } from "react";
import { Row, Col, Card, Button } from "react-bootstrap";
import "@fullcalendar/react";
import { DateClickArg, Draggable } from "@fullcalendar/interaction";
import { EventClickArg, EventInput } from "@fullcalendar/core";
import classNames from "classnames";
import PageTitle from "../../../components/PageTitle";

import Calendar from "./Calendar";
import AddEditEvent from "./AddEditEvent";

import { deleteEventById, getAllEvents, postEvent, putEvent } from "./helper";

const CalendarApp = () => {
  /*
   * modal handeling
   */
  const [show, setShow] = useState<boolean>(false);
  const onCloseModal = () => {
    setShow(false);
    setEventData({});
    setDateInfo({});
  };
  const onOpenModal = () => setShow(true);
  const [isEditable, setIsEditable] = useState<boolean>(false);

  /*
   * event data
   */
  const [events, setEvents] = useState<EventInput[]>([]);
  const [eventData, setEventData] = useState<EventInput>({});
  const [dateInfo, setDateInfo] = useState<any>({});

  useEffect(() => {
    // create dragable events
    let draggableEl = document.getElementById("external-events");
    new Draggable(draggableEl!, {
      itemSelector: ".external-event",
    });
    getAllEvents().then((response) => setEvents(response));
  }, []);

  /*
    calendar events
    */
  // on date click
  const onDateClick = (arg: DateClickArg) => {
    setDateInfo(arg);
    onOpenModal();
    setIsEditable(false);
  };

  // on event click
  const onEventClick = (arg: EventClickArg) => {
    const event = {
      id: String(arg.event.id),
      title: arg.event.title,
      className: arg.event.classNames[0],
    };
    setEventData(event);
    setIsEditable(true);
    onOpenModal();
  };

  const onDrop = (arg: any) => {
    const dropEventData = arg;
    const title = dropEventData.draggedEl.title;
    if (title == null) {
      return;
    }

    const start = dropEventData.date;
    const end = new Date(start.getTime() + 60 * 60 * 1000); // Adding 1 hour to start time

    const newEvent = {
      id: String(events.length + 1),
      title: title,
      start: start,
      end: end,
      className: dropEventData.draggedEl.attributes["data-class"]["value"],
    };
    postEvent(newEvent).then(() => {
      getAllEvents().then(setEvents);
    });
    // const modifiedEvents = [...events, newEvent];
    // setEvents(modifiedEvents);
  };;

  /*
    on add event 
    */
  const onAddEvent = (data: any) => {
    const modifiedEvents = [...events];
    const event = {
      // id: String(modifiedEvents.length + 1),
      title: data.title,
      start: Object.keys(dateInfo).length !== 0 ? dateInfo.date : new Date(),
      end: new Date(
        (Object.keys(dateInfo).length !== 0
          ? dateInfo.date
          : new Date()
        ).getTime() +
          60 * 60 * 1000
      ),
      className: data.className,
    };

    postEvent(event);
    modifiedEvents.push(event);
    setEvents(modifiedEvents);
    onCloseModal();
  };

  /*
    on update event
    */
  const onUpdateEvent = (data: any) => {
    const modifiedEvents = [...events];
    const idx = modifiedEvents.findIndex((e: any) => e["id"] === eventData!.id);
    modifiedEvents[idx]["title"] = data.title;
    modifiedEvents[idx]["className"] = data.className;
    const updatedEvent = modifiedEvents[idx];
    putEvent(updatedEvent.id, updatedEvent);
    setEvents(modifiedEvents);
    onCloseModal();
    setIsEditable(false);
  };

  /*
    on remove event
    */
  const onRemoveEvent = () => {
    var modifiedEvents = [...events];
    const idx = modifiedEvents.findIndex((e: any) => e["id"] === eventData!.id);
    deleteEventById(eventData!.id);
    modifiedEvents.splice(idx, 1);
    setEvents(modifiedEvents);
    onCloseModal();
  };

  /**
   * on event drop
   */
  const onEventDrop = (arg: any) => {
    const modifiedEvents = [...events];
    const idx = modifiedEvents.findIndex((e) => e["id"] === arg.event.id);
    modifiedEvents[idx]["title"] = arg.event.title;
    modifiedEvents[idx]["className"] = arg.event.classNames;
    modifiedEvents[idx]["start"] = arg.event.start;
    modifiedEvents[idx]["end"] = arg.event.end;
    setEvents(modifiedEvents);
    putEvent(modifiedEvents[idx]?.id, {
      ...modifiedEvents[idx],
      className: modifiedEvents[idx]?.className?.[0],
    });
    setIsEditable(false);
  };

  // create new event
  const createNewEvent = () => {
    setIsEditable(false);
    onOpenModal();
  };

  const externalEvents = [
    {
      id: 1,
      className: "bg-success",
      title: "Client Meeting",
    },
    {
      id: 2,
      className: "bg-info",
      title: "Productivity",
    },
    {
      id: 3,
      className: "bg-warning",
      title: "Scrum",
    },
    {
      id: 4,
      className: "bg-danger",
      title: "Break",
    },
  ];

  const handleEventResize = (e: any) => {
    const title = e?.event?._def?.title;
    const id = e?.event?._def?.publicId;
    const start = e?.event?._instance?.range?.start;
    const end = e?.event?._instance?.range?.end;
    const className = e?.event?.ui?.classNames?.[0];
    const event: any = { id, title, start, end, className };
    console.log(event);
    if (!id) {
      postEvent(event);
    } else {
      putEvent(id, event);
    }

    // Add your logic here to update the event or perform any other action
  };

  return (
    <>
      <PageTitle
        breadCrumbItems={[
          { label: "Apps", path: "/apps/calendar" },
          { label: "Calendar", path: "/apps/calendar", active: true },
        ]}
        title={"Calendar"}
      />

      <Row>
        <Col>
          <Card>
            <Card.Body>
              <Row>
                <Col lg={3}>
                  {/* add events */}
                  <Button
                    className="btn btn-lg font-16 btn-primary w-100"
                    id="btn-new-event"
                    onClick={createNewEvent}
                  >
                    <i className="mdi mdi-plus-circle-outline"></i> Create New
                    Event
                  </Button>

                  <>
                    <div id="external-events">
                      <br />

                      {/* external events */}
                      {(externalEvents || []).map((event, index) => {
                        return (
                          <div
                            key={index}
                            className={classNames(
                              "external-event",
                              event.className
                            )}
                            title={event.title}
                            data-class={event.className}
                          >
                            <i className="mdi mdi-checkbox-blank-circle me-2 vertical-middle"></i>
                            {event.title}
                          </div>
                        );
                      })}
                    </div>

                    {/* <div className="mt-5 d-none d-xl-block">
                      <h5 className="text-center">How It Works ?</h5>

                      <ul className="ps-3">
                        <li className="text-muted mb-3">
                          It has survived not only five centuries, but also the
                          leap into electronic typesetting, remaining
                          essentially unchanged.
                        </li>
                        <li className="text-muted mb-3">
                          Richard McClintock, a Latin professor at
                          Hampden-Sydney College in Virginia, looked up one of
                          the more obscure Latin words, consectetur, from a
                          Lorem Ipsum passage.
                        </li>
                        <li className="text-muted mb-3">
                          It has survived not only five centuries, but also the
                          leap into electronic typesetting, remaining
                          essentially unchanged.
                        </li>
                      </ul>
                    </div> */}
                  </>
                </Col>
                <Col lg={9}>
                  {/* fullcalendar control */}
                  <Calendar
                    onDateClick={onDateClick}
                    onEventClick={onEventClick}
                    onDrop={onDrop}
                    onEventDrop={onEventDrop}
                    events={events}
                    onEventResize={handleEventResize}
                  />
                </Col>
              </Row>
            </Card.Body>
          </Card>
        </Col>
      </Row>

      {/* add new event modal */}
      {show ? (
        <AddEditEvent
          isOpen={show}
          onClose={onCloseModal}
          isEditable={isEditable}
          eventData={eventData}
          onUpdateEvent={onUpdateEvent}
          onRemoveEvent={onRemoveEvent}
          onAddEvent={onAddEvent}
        />
      ) : null}
    </>
  );
};

export default CalendarApp;
